:root {
  --font-body: "Roboto", sans-serif;
}

.material-icons {
  font-family: "Material Icons";
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  font-feature-settings: "liga";
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --header-bg-dark: hsl(0, 0%, 13%);
  --header-search-btn: hsl(0, 0%, 20%);
  --header-search-placeholder: hsl(0, 0%, 40%);
  --header-search-input: hsl(0, 0%, 7%);
  --body-bg-dark: hsl(0, 0%, 9%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 100vw;
  margin: 0 auto;
  border: 2px solid black;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.flex--row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo__hamburger {
  padding: 0.5rem;
}

.header__logo__youtube {
  padding: 1rem;
}

.logo__youtube {
  width: 5.625rem;
  height: 1.25rem;
}

.header__search__input__container {
  min-width: 35.9375rem;
  min-height: 2rem;
}
.header__search__input__container input {
  flex: 1;
  height: 2rem;
}
.header__search__input__container .search__icon {
  text-align: center;
  width: 4.0625rem;
}

.header__search__voice {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  text-align: center;
}